*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


html, body {
    height: 100%;
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}


ul, ol {
    list-style: none;
}


a {
    text-decoration: none;
    color: inherit;
}


img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}


input, button, textarea, select {
    font: inherit;
}
