.photo-card {
    border: 1px solid gray;
    margin: 30px;
    padding: 10px;
    border-radius: 8px;
    background-color: #f9f9f9;
    width: 600px;
}


.photo-header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 5px;
    text-align: left;
}

    .photo-header img {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin-right: 10px;
    }

.photo-title {
    font-size: 18px;
    margin-bottom: 15px;
    text-align: center;
    color: #5c5858;
   
}

.photo-author {
    font-weight: bold;
}

.photo-image {
    display: block;
    margin-bottom: 10px;
    max-width: 100%;
    border-radius: 6px;
}

.photo-description {
    margin-bottom: 10px;
}


.like-button {
    cursor: pointer;
    font-size: 20px;
    transition: transform 0.2s ease;
}

    .like-button:hover {
        transform: scale(1.4);
    }


.vote-section {
    display: flex;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 10px;
}

.vote-text {
    margin-left: 10px;
    font-size: 14px;
    color: #555;
    transition: color 0.3s ease;
}

    .vote-text.voted {
        color: #2e8b57;
        font-weight: bold;
    }

