.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2e8b57;
    padding: 10px 20px;
    color: white;
}

.navbar-logo {
    font-size: 20px;
    font-weight: bold;
}

.navbar-links {
    list-style: none;
    display: flex;
    gap: 15px;
    margin: 0;
    padding: 0;
}

    .navbar-links li a {
        color: white;
        text-decoration: none;
        font-weight: 500;
        transition: color 0.3s ease;
    }

        .navbar-links li a:hover {
            color: #ffd700;
        }
